
import Icon from 'vux/src/components/icon/index.vue'
import Toast from 'vux/src/components/toast/index.vue'
import Alert from 'vux/src/components/alert/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

export default {
name: '',
directives: {
TransferDom
},
components: {
Icon,
Toast,
Alert
},
data () {
return {
toastShow: false,
alertShow: false,
searchValue: '',
searchShow: false,
asideNav: false,
navs: [
{
title: '关于Belli',
lists: [
{
title: '品牌故事',
name: 'story',
path: 'story'
},
{
title: '安全筛选',
name: 'safe',
path: 'safe'
},
{
title: 'Belli在美国',
name: 'america',
path: 'america'
},
{
title: 'Belli在中国',
name: 'china',
path: 'china'
}
],
status: false
},
{
title: '全选产品',
lists: [
{
title: '妈妈系列',
name: 'product1',
path: 'product1',
pid: 105,
val: ''
},
{
title: '婴幼儿系列',
name: 'product2',
path: 'product2',
pid: 106,
val: ''
}
],
status: false
},
{
title: '孕期护理',
lists: [
{
title: '孕纹防护专题',
name: 'yunwen',
path: 'yunwen'
},
{
title: '面部护理专题',
name: 'facial',
path: 'facial'
}
],
status: false
},
{
title: '婴幼儿护理',
lists: [
{
title: '宝宝洗护专题',
name: 'baby',
path: 'baby'
}
],
status: false
},
{
title: '品牌动态',
lists: [
{
title: '品牌资讯',
name: 'news',
path: 'news',
pid: 1
},
{
title: '优生优育',
name: 'news',
path: 'news',
pid: 2
},
{
title: '孕期百科',
name: 'news',
path: 'news',
pid: 3
}
],
status: false
},
{
title: '会员服务',
lists: [
{
title: '正品验证',
name: 'qualityTest',
path: 'qualityTest'
},
{
title: '试用中心',
name: 'trail',
path: 'trail'
}
],
status: false
}
]
}
},
async created () {
let result = await this.axios.post('product/proclasslist')
let data = result.data
let series = []
data.map((item, index) => {
if (item.pid / 1 === 0) { // 获取所有系列
series.push({
title: item.title,
name: 'product',
path: 'product',
pid: item.id,
val: ''
})
}
})
this.navs[1].lists = series
console.log(series)
},
methods: {
searchIpt () { // 搜索
console.log(this.searchValue)
},
submit () {
let route = this.$route
if (this.searchValue === '') {
this.toastShow = true
return false
}
this.searchShow = false
console.log(this.$route, route.name === 'product')
if (route.name === 'product') { // 若在产品组件页搜索
this.$router.push({name: 'product', params: {pid: route.params.pid, val: this.searchValue}})
} else { // 否则跳到全线产品
this.$router.push({name: 'product', params: {pid: '0', val: this.searchValue}})
}
this.searchValue = ''
console.log('sumit')
},
link (item, index) { // 产品详情

this.$router.push({name: item.name, params: {'pid': item.pid}})
},
searchBox () { // 搜索框
this.searchShow = !this.searchShow
},
aside () { // 侧边栏显隐
if (this.asideNav) {
this.close()
} else {
this.open()
}
},
open () { // 侧边栏打开
this.asideNav = true

document.querySelector('html').style.cssText += 'overflow-y:hidden;'
document.querySelector('body').style.cssText += 'overflow-y:hidden;'
document.querySelector('#app').style.left = '200px'
document.querySelector('header').style.left = '200px'
},
close () { // 侧边栏关闭
this.asideNav = false
this.searchShow = false
document.querySelector('html').style.cssText += 'overflow-y:auto;'
document.querySelector('body').style.cssText += 'overflow-y:auto;'
document.querySelector('#app').style.left = 0
document.querySelector('header').style.left = 0
},
navListsSHow (navIndex) { // navBar 点击下拉
this.navs.filter((item, index) => {
if (index === navIndex) {
let result = item.status = !item.status
return result
}

})
}
}
}



