export default {
  name: '',
  components: {},
  data () {
    return {
      navs: [
        {
          title: '关于Belli',
          url: '',
          lists: [
            {
              title: '品牌故事',
              url: 'story'
            },
            {
              title: '安全筛选',
              url: 'safe'
            },
            {
              title: '品牌动态',
              url: 'news',
              pid: 1
            }
          ],
          status: false
        },
        {
          title: '全线产品',
          url: '/product/0/empty',
          lists: [],
          status: false
        },
        {
          title: '客户服务',
          url: '',
          lists: [
            {
              title: '正品验证',
              url: 'qualityTest'
            },
            {
              title: '我的订单',
              url: 'order'
            },
            {
              title: '售后服务',
              url: 'faq'
            },
            {
              title: '配送服务',
              url: 'faq'
            }
          ],
          status: false
        }
      ]
    }
  },
  methods: {
    close () {
      console.log('close')
    },
    navListsSHow (navIndex) {
      // navBar 点击下拉
      this.navs.filter((item, index) => {
        if (index === navIndex) {
          let result = (item.status = !item.status)
          return result
        }
        console.log(item, index)
      })
    },
    backTop () {
      window.scrollTo(0, 0)
    }
  }
}



