
import HeaderNav from '@/components/header.vue'
import BottomMes from '@/components/bottom.vue'
import FooterNav from '@/components/FooterBar.vue'
import Toast from 'vux/src/components/toast/index.vue'

export default {
name: 'app',
components: {
Toast,
HeaderNav,
BottomMes,
FooterNav
},
data () {
return {
showToast: false
}
},
async created () { // 运费
{
let result = await this.axios.post(this.base_url + '/config/syssetlist')
if (result.success) {
let data = result.data
sessionStorage.setItem('nofreight', data[0].value) 
sessionStorage.setItem('freight', data[1].value) 
localStorage.setItem('nofreight', data[0].value) 
localStorage.setItem('freight', data[1].value) 
}
}
